@import url("https://fonts.googleapis.com/css?family=Questrial");
@keyframes showItems {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  outline: none; }
  *:before, *:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; }

html body * {
  font-family: 'Questrial', sans-serif; }

html {
  font-size: 1rem;
  -webkit-tap-highlight-color: transparent; }

body {
  margin: 0; }

html, body {
  width: 100%;
  height: 100%;
  background-color: #0f0f0f;
  font-weight: normal;
  color: #ffffff; }

h1, h2, h3, h4, h5, h6 {
  font-weight: normal; }

::-webkit-scrollbar {
  width: 0.2rem;
  height: 0.2rem; }

::-webkit-scrollbar-thumb {
  background: #919191; }

::-webkit-scrollbar-track {
  background: #c8c8c8; }

body {
  scrollbar-face-color: transparent;
  scrollbar-track-color: transparent; }

#teaser {
  text-align: center;
  padding: 40px;
  display: table; }
  #teaser article {
    display: table-cell;
    vertical-align: middle; }
    #teaser article img {
      max-width: 100px;
      animation-name: showItems;
      animation-duration: 2s; }
    #teaser article h1 {
      font-size: 2rem;
      animation-name: showItems;
      animation-duration: 2s; }
    #teaser article h2 {
      font-size: 1.3rem;
      animation-name: showItems;
      animation-duration: 2s;
      margin-top: -1rem; }
    #teaser article p {
      margin-top: 4rem;
      font-size: 1rem;
      animation-name: showItems;
      animation-duration: 2s; }
